@import '../../styles/index.scss';

.loading-spinner {
    justify-content: center;
    align-items: center;
    display: flex;
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: white;
    opacity: 1;
    overflow: hidden;
    z-index: 999;

    &__content {
        border: 8px solid $primary-color;
        border-left-color: black;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        animation: spin 1s linear infinite;
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
  }